@mixin titleSize($size) {
  font-size: #{$size};
  line-height: #{$size};
}

@mixin onTablet {
  @media (min-width:744px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1260px) {
    @content;
  }
}

@mixin top-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
